<template>
    <nav class="flex items-center justify-between flex-wrap p-5">
        <div class="flex items-center flex-shrink-0 mx-5">
          <img src="/images/logo.png" alt="Armada Offensive" width="200px">
        </div>
        <div class="w-full block flex-grow lg:flex lg:items-center lg:w-auto">
          <div class="lg:flex-grow">
            <router-link active-class="nav-link-active" class="nav-link" to="/" exact>Mixtones</router-link>
            <div v-if="!lobby" active-class="nav-link-active cursor-pointer" class="nav-link cursor-pointer" @click="createLobbyOnce">Crear Lobby</div>
            <router-link v-else active-class="nav-link-active" class="nav-link" to="/lobby">Lobby</router-link>
            <router-link active-class="nav-link-active" class="nav-link" to="/servers">Servers</router-link>
            <router-link active-class="nav-link-active" class="nav-link" to="/news">Noticias</router-link>
          </div>
          
        </div>
      </nav>
</template>

<script>
import { mapState } from 'vuex'
import _ from 'lodash'

export default {
    name: 'Navbar',
    data(){
        return {

        }
    },
    computed: mapState([
      'lobby'
    ]),
    mounted(){

    },
    methods:{
        createLobbyOnce: _.debounce(function(){ 
            if(!this.lobby){
                this.$router.push('/lobby')
            }           
            
        },2000,{
        'leading': true
        })
    }
}
</script>